.App {
  height: 100vh;
  margin: 0;
  text-align: center;
}

.App-header {
  background-image: url("./media/pictures/israelPalmTrees.jpg");

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
}

.App-words {
  font-size: 9em;
  text-align: center;
  color: #66ff00;
  font: "Lucida Console", Monaco, monospace;
}

#rowContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: center;
      align-items: center;
}

#rowContent {
  -ms-flex-item-align: center;
      align-self: center;
  width: 25%;
}

.headerRowContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: center;
      align-items: center;
}

@media screen and (max-width:700px) {
  #rowContainer { -ms-flex-wrap: wrap; flex-wrap: wrap;  }
  #rowContent { -ms-flex-preferred-size: 100%; flex-basis: 100%; }
}



